.socialBox {
  @media screen and (min-width: 1410px) {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    align-self: center;
  }

  & p {
    @media screen and (max-width: 1409px) {
      display: none;
    }
    font-size: 14px;
    line-height: 24px;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    margin-top: 18px;
    color: #767676;
  }
  & ul > a {
    @media screen and (max-width: 1409px) {
      margin-right: 10px;
      &:last-child {
        margin-right: 0;
      }
    }
    @media screen and (min-width: 1410px) {
      margin-top: 25px;
      display: block;
      &:first-child {
        margin-top: 0;
      }
    }
  }
}
